.sc-body {
  display: -ms-flexbox;
  display: flex;
  margin-top: 50px;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.oh-so-hidden {
  position: absolute;
  bottom: 0;
  right: 0;
}

.oh-so-hidden:hover {
  cursor: pointer;
}

.sc-body__syncindicator {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 10px;
  height: 10px;
  background-color: black;
}

.sc-body__syncindicator--synced {
  background-color: green;
}

.sc-body__syncindicator--not-synced {
  background-color: red;
}



